.app {
  margin: 20px auto;
  display: flex;
  place-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}
/* small */
@media (min-width: 768px){
  .app {
      width: 750px;
  }
}
/* Meduim*/
@media (min-width : 992px){
  .app {
      width: 970px;
  }
}
/* Large */
@media (min-width: 1200px){
  .app{
      width: 1170px;
  }
}
.btn {
  border: 1px solid transparent;
  background-color: rgb(41, 41, 41);
  color: white;
  border-radius: 3px;
  font-family:  sans-serif;
}
.addTask {
  padding: 10px;
  background-color: dodgerblue;
  border-radius: 5px;
  width: 286px;
}
.addTask button {
  margin-left: 10px;
}
ul {
  list-style: none;
  background-color: gray;
  border-radius: 3px;
  width: 286px;
  color: whitesmoke;
}
ul li {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  justify-content: space-between;
  gap: 10px;
  margin-top: 10px;
  margin-bottom: 10px;
  position: relative;
}
ul li .removeBtn {
  background-color: gray;
  border: 1px solid transparent;
  border-radius: 50%;
  color: rgb(165, 21, 21);
  font-family: cursive;
  position: absolute;
  left: -30px;
}
ul li .removeBtn:hover  {
  border-color: rgb(116, 116, 116) ;
  background-color: rgb(116, 116, 116)  ;
}
.compltBtn {
  margin-right: 10px;
}